"use client";

import { Connector, useConnect } from "@starknet-react/core";
import { StarknetkitConnector, useStarknetkitConnectModal } from "starknetkit";

const buttonStyles =
  "bg-[#e2a127] border-black border rounded-lg p-2 transition-shadow hover:shadow-[6px_6px_0_0_rgba(0,0,0,1)] cursor-pointer";

import React from "react";

export function WalletConnectorModal() {
  const { connect, connectors } = useConnect();

  const { starknetkitConnectModal } = useStarknetkitConnectModal({
    connectors: connectors as StarknetkitConnector[],
  });

  async function connectWallet() {
    const { connector } = await starknetkitConnectModal();
    if (!connector) {
      return;
    }
    await connect({ connector: connector as Connector });
  }

  return (
    <button onClick={connectWallet} className={buttonStyles}>
      Connect Wallet
    </button>
  );
}
